.playBtn {
    display: inline-block;
    position: relative;
    width: 1em;
    height: 1em;
    border: 0.05em solid currentColor;
    border-radius: 50%;
    color: #000;
    font-size: 50px;
    &::before{
        position: absolute;
        top: 50%;
        left: 30%;
        transform: translateY(-50%);
        width: 0px;
        height: 0px;
        border: 0.3em solid transparent;
        border-left: 0.5em solid currentColor;
        box-sizing: border-box;
        content: "";
    }
    
    cursor: pointer;
}